import React from "react"
import ArcadeButton from "../../../components/ArcadeButton"
import css from "./styles.module.scss"
import cx from "classnames"

const ButtonClicks = ({ onDone, clicks = 50 }) => {
  const [currentClicks, setCurrentClicks] = React.useState(0)
  const handleClick = () => {
    if (currentClicks >= clicks) {
      onDone()
    } else {
      setCurrentClicks((c) => c + 1)
    }
  }

  const percDone = (currentClicks / clicks) * 100

  return (
    <div className={css.container}>
      <div className={css.waves}>
        <div className={cx(css.wave, css.waveBack)}>
          <div className={css.water} style={{ height: `${percDone}%` }}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 350 32" preserveAspectRatio="none">
              <path d="M350,17.32V32H0V17.32C116.56,65.94,175-39.51,350,17.32Z" />
            </svg>
          </div>
          <div className={css.water} style={{ height: `${percDone}%` }}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 350 32" preserveAspectRatio="none">
              <path d="M350,17.32V32H0V17.32C116.56,65.94,175-39.51,350,17.32Z" />
            </svg>
          </div>
        </div>
        <div className={cx(css.wave, css.waveFront)}>
          <div className={css.water} style={{ height: `${percDone}%` }}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 350 32" preserveAspectRatio="none">
              <path d="M350,17.32V32H0V17.32C116.56,65.94,175-39.51,350,17.32Z" />
            </svg>
          </div>
          <div className={css.water} style={{ height: `${percDone}%` }}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 350 32" preserveAspectRatio="none">
              <path d="M350,17.32V32H0V17.32C116.56,65.94,175-39.51,350,17.32Z" />
            </svg>
          </div>
        </div>
      </div>
      <ArcadeButton className={css.button} variant="gray" glow={true} onClick={handleClick}></ArcadeButton>
    </div>
  )
}

export default ButtonClicks
